import React from "react";
import { Route, Switch } from "react-router-dom";

// components
import Buttons from "./components/Inputs/Buttons";
import Checkboxes from "./components/Inputs/Checkbox";
import TextField from "./components/Inputs/TextField";
import Links from "./components/Navigation/Links";
import DateAndTime from "./components/Inputs/DateAndTime";
import Card from "./components/Surfaces/Card";
import PopOver from "./components/PopOver";
import ButtonGroup from "./components/Inputs/ButtonGroup";
import FloatingActionButton from "./components/Inputs/FloatingActionButton";
import Radio from "./components/Inputs/Radio";
import Select from "./components/Inputs/Select";
import SwitchButton from "./components/Inputs/Switch";
import Tabs from "./components/Navigation/Tabs";
import Panel from "./components/CustomComponents/Panel/Test";
import BottomNavigation from "./components/Navigation/BottomNavigation";
import AppBar from "./components/Surfaces/AppBar";
import PersistentDrawer from "./components/PersistentDrawer";
import Badge from "./components/DataDisplay/Badge";
import Chip from "./components/DataDisplay/Chip";
import TypographyComp from "./components/DataDisplay/Typography.js";
import Transitions from "./components/Utils/Transitions";
import Stepper from "./components/Navigation/Stepper";

// custom components
import Table from "./components/CustomComponents/Table/MuiTable";
import PanelOpen from "./components/CustomComponents/Panel/PanelOpen";
import CustomStepper from "./components/CustomComponents/CustomStepper";

const Main = ()=>{
  return(
    <div style={{display:"flex",justifyContent:"center"}}>
      <p style={{color:"gray",letterSpacing:"3px",fontSize:"30px"}}>Select a component</p>
    </div>
  )
}

const NotFound = ()=>{
    return(
      <div style={{display:"flex",justifyContent:"center"}}>
        <p style={{color:"gray",letterSpacing:"3px",fontSize:"30px"}}>Not Found</p>
      </div>
    )
}

export default function App() {
    return (
        <PersistentDrawer>
            <Switch>
                <Route exact path="/theme/" component={Main}/>
                <Route exact path="/theme/buttons" component={Buttons} />
                <Route exact path="/theme/checkbox" component={Checkboxes}/>
                <Route exact path="/theme/textfield" component={TextField}/>
                <Route exact path="/theme/links" component={Links}/>
                <Route exact path="/theme/dateandtime" component={DateAndTime}/>
                <Route exact path="/theme/card" component={Card}/>
                <Route exact path="/theme/popover" component={PopOver}/>
                <Route exact path="/theme/buttongroup" component={ButtonGroup}/>
                <Route exact path="/theme/floatingactionbutton" component={FloatingActionButton}/>
                <Route exact path="/theme/radio" component={Radio}/>
                <Route exact path="/theme/select" component={Select}/>
                <Route exact path="/theme/switch" component={SwitchButton}/>
                <Route exact path="/theme/tabs" component={Tabs}/>
                <Route exact path="/theme/panel" component={Panel}/>
                <Route exact path="/theme/bottomnavigation" component={BottomNavigation}/>
                <Route exact path="/theme/appbar" component={AppBar}/>
                <Route exact path="/theme/badge" component={Badge}/>
                <Route exact path="/theme/chip" component={Chip}/>
                <Route exact path="/theme/typography" component={TypographyComp}/>
                <Route exact path="/theme/transitions" component={Transitions}/>
                <Route exact path="/theme/stepper" component={Stepper}/>
                {/* Custom components---------- */}
                <Route exact path="/theme/panel/:id" component={PanelOpen} />
                <Route exact path="/theme/table" component={Table} />
                <Route exact path="/theme/customstepper" component={CustomStepper} />
                <Route component={NotFound}/>
            </Switch>
        </PersistentDrawer>  
    );
}
