import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepConnector from '@material-ui/core/StepConnector';




const CustomStepConnector = withStyles({
  root:{
    display:"flex",
    justifyContent:"center",
  },
  alternativeLabel: {
    top: 22,
  },
  active: {
    '& $line': {
      // backgroundColor:"#64A50F"
    },
  },
  completed: {
    '& $line': {
      borderColor: "#64A50F"
    },
  },
  line: {
    width:"50%",
    height: 3,
    padding:"20 20px",
    border: 0,
    backgroundColor: '#eaeaf0',
    borderRadius: 1,
  },
})(StepConnector);

const useCustomStepIconStyles = makeStyles({
  root: {
    background:"white",
    border: '2px solid #ccc',
    zIndex: 1,
    color: 'gray',
    width: 150,
    height: 45,
    display: 'flex',
    borderRadius: '23px',
    justifyContent: 'center',
    alignItems: 'center',
    cursor:"pointer",
    fontWeight:"bold",
  },
  active: {
    borderColor: "#64A50F",
    color:"#64A50F",
  },
  completed: {
    // borderColor: "#64A50F"
  },
});



// container styles--------------------------------------
const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  button: {
    marginRight: theme.spacing(1),
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
}));

function getSteps() {
  return ['Select campaign settings', 'Create an ad group', 'Create an ad',"New Step"];
}


export default function CustomizedSteppers() {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();


  function CustomStepIcon(props) {
    const classes = useCustomStepIconStyles();
    const { active, completed } = props;
    const icons = {
      "1": 1,
      "2": 2,
      "3": 3,
      "4": 4,
    };
  
    return (
      <div onClick={()=>{setActiveStep(props.icon-1)}}
        className={clsx(classes.root, {
          [classes.active]: active,
          [classes.completed]: completed,
        })}
      >
        {icons[props.icon]}
      </div>
    );
  }

  return (
    <div className={classes.root}>
      {/* custom stepper ----------------------------------------------*/}
      <Stepper  connector={<CustomStepConnector />} alternativeLabel activeStep={activeStep}>
        {steps.map((label) => (
          <Step  key={label}>
            <StepLabel StepIconComponent={CustomStepIcon} ></StepLabel>
          </Step>
        ))}
      </Stepper>    
    </div>
  );
}

