import React from 'react';
import ComponentWrapper from "../../ComponentWrapper";


// 
import BasicBadge from "./BasicBadge";
import CustomizedBadge from "./CustomizedBadge";
import BadgeVisibility from "./BadgeVisibility";
import MaximumValue from "./MaximumValue";
import DotBadge from "./DotBadge";

export default function index() {
    return (
        <div>
            <ComponentWrapper name="Basic Badge">
                <BasicBadge/>
            </ComponentWrapper>
            <ComponentWrapper name="Customized Badge">
                <CustomizedBadge/>
            </ComponentWrapper>
            <ComponentWrapper name="Badge visibility">
              <BadgeVisibility/>  
            </ComponentWrapper>
            <ComponentWrapper name="Maximum value">
                <MaximumValue/>
            </ComponentWrapper>
            <ComponentWrapper name="Dot Badge">
                <DotBadge/>
            </ComponentWrapper>
        </div>
    )
}
