import React from 'react';

// components
import ComponentWrapper from "../../ComponentWrapper";
import BasicButtonGroup from "./BasicButtonGroup";
import ButtonGroupSizes from "./ButtonGroupSizes";
import VerticalButtonGroup from "./VerticalButtonGroup";
import SplitButton from "./SplitButton";
import DisabledElevation from "./DisabledElevation";

export default function ButtonGroup() {
    return (
        <div>
            <ComponentWrapper name="Basic Button Group">
                <BasicButtonGroup/>
            </ComponentWrapper>
            <ComponentWrapper name="Sizes">
                <ButtonGroupSizes/>
            </ComponentWrapper>
            <ComponentWrapper name="Vertical">
                <VerticalButtonGroup/>  
            </ComponentWrapper>
            <ComponentWrapper name="Split Button">
                <SplitButton/>
            </ComponentWrapper>
            <ComponentWrapper name="Disabled elevation">
                <DisabledElevation/>
            </ComponentWrapper>
        </div>
    )
}
