import React from "react";

// components
import ComponentWrapper from "../../ComponentWrapper";
import BasicCheckboxes from "./BasicCheckboxes";
import CheckboxFormLabel from "./CheckboxFormLabel";
import CheckboxFormGroup from "./CheckboxFormGroup";

export default function Checkboxes() {
    return (
        <div>
            <ComponentWrapper name="Basic Checkboxes">
                <BasicCheckboxes />
            </ComponentWrapper>
            <ComponentWrapper name="Checkbox with FormControlLabel">
                <CheckboxFormLabel />
            </ComponentWrapper>
            <ComponentWrapper name="Checkboxes with FormGroup">
                <CheckboxFormGroup />
            </ComponentWrapper>
          
        </div>
    );
}
