import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TablePagination from '@material-ui/core/TablePagination';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import Avatar from '@material-ui/core/Avatar';
import {Grid} from "@material-ui/core";

// table components
import EnhancedTableHead from "./TableHead";
import EnhancedTableToolbar from "./ToolBar";
import ListStatusItem from "./ListStatusItem";
// form dialog
import FormDialog from "./FormDialog";

function createData(id, name, calories, type, associated, date) {
  return { id, name, calories, type, associated, date };
}

const rows = [
  createData('1', 'Completed', "Office meet-up", "Reminder", "Lindsey Stroud", "Dec 14 2020"),
  createData('2', 'Ended', "Call to check-in", "Call", "George Fields", "Dec 14 2020"),
  createData('3', 'Active', "Office meet-up", "Event", "Lindsey Stroud", "Dec 14 2020"),
  createData('4', 'Ended', "Call to check-in", "Call", "George Fields", "Dec 14 2020"),
  createData('5', 'Completed', "Call to check-in", "Event", "Lindsey Stroud", "Dec 14 2020"),
  createData('6', 'Active', "Call to check-in", "Call", "George Fields", "Dec 14 2020"),
  createData('7', 'Ended', "Office meet-up", "Event", "Lindsey Stroud", "Dec 14 2020"),
  createData('8', 'Completed', "Call to check-in", "Event", "George Fields", "Dec 14 2020"),
  createData('9', 'Ended', "Call to check-in", "Event", "Lindsey Stroud", "Dec 14 2020"),
  createData('10', 'Ended', "Office meet-up", "Reminder", "George Fields", "Dec 14 2020"),
  createData('11', 'Completed', "Office meet-up", "Reminder", "Lindsey Stroud", "Dec 14 2020"),
  createData('12', 'Active', "Call to check-in", "Event", "George Fields", "Dec 14 2020"),
  createData('13', 'Ended', "Office meet-up", "Reminder", "Lindsey Stroud", "Dec 14 2020"),
];



function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, orderBy) {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
}

function stableSort(array, comparator) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
}



const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  paper: {
    width: '100%',
    marginBottom: theme.spacing(2),
  },
  table: {
    minWidth: 750,
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
  btnWarning:{
    backgroundColor: theme.palette.warning.main,
    color:theme.palette.common.white,
    "&:hover":{
      backgroundColor: theme.palette.warning.dark,
    }
  },
  btnSecondary:{
    backgroundColor: theme.palette.secondary.main,
    color:theme.palette.common.white,
  },
  btnSuccess:{
    backgroundColor: theme.palette.success.main,
    color:theme.palette.common.white,
    "&:hover":{
      backgroundColor: theme.palette.success.dark,
    }
  }
}));



export default function EnhancedTable() {
  const classes = useStyles();
  const [order, setOrder] = React.useState('asc');
  const [orderBy, setOrderBy] = React.useState('calories');
  const [selected, setSelected] = React.useState([]);
  const [page, setPage] = React.useState(0);
  const [dense, setDense] = React.useState(false);
  const [rowsPerPage, setRowsPerPage] = React.useState(5);
  //formDialog
  const [formOpen,setFormOpen] = React.useState(false);
  // filter
  const [filter, setFilter] = React.useState("All");

  const handleFilterChange = (e) => {
    console.log(e.target.value)
    setFilter(e.target.value)
  }
  
  const handleCreateTask = (status)=>{
    setFormOpen(status)
  }

  const handleRequestSort = (event, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const handleSelectAllClick = (event) => {
    if (event.target.checked) {
      const newSelecteds = rows.map((n) => n.id);
      setSelected(newSelecteds);
      return;
    }
    setSelected([]);
  };

  const handleClick = (event, name) => {
    const selectedIndex = selected.indexOf(name);
    let newSelected = [];

    if (selectedIndex === -1) {
      newSelected = newSelected.concat(selected, name);
    } else if (selectedIndex === 0) {
      newSelected = newSelected.concat(selected.slice(1));
    } else if (selectedIndex === selected.length - 1) {
      newSelected = newSelected.concat(selected.slice(0, -1));
    } else if (selectedIndex > 0) {
      newSelected = newSelected.concat(
        selected.slice(0, selectedIndex),
        selected.slice(selectedIndex + 1),
      );
    }

    setSelected(newSelected);
  };

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  const handleChangeDense = (event) => {
    setDense(event.target.checked);
  };

  const isSelected = (name) => selected.indexOf(name) !== -1;

  const emptyRows = rowsPerPage - Math.min(rowsPerPage, rows.length - page * rowsPerPage);

  
  return (
    <div className={classes.root}>
      <FormDialog handleCreateTask={handleCreateTask} open={formOpen}  />
      <Paper className={classes.paper}>
        <EnhancedTableToolbar handleFilterChange={handleFilterChange} handleCreateTask={handleCreateTask} numSelected={selected.length} />
        <TableContainer>
          <Table
            className={classes.table}
            aria-labelledby="tableTitle"
            size={dense ? 'small' : 'medium'}
            aria-label="enhanced table"
          >
            <EnhancedTableHead
              classes={classes}
              numSelected={selected.length}
              order={order}
              orderBy={orderBy}
              onSelectAllClick={handleSelectAllClick}
              onRequestSort={handleRequestSort}
              rowCount={rows.length}
            />
            <TableBody>
              {stableSort(rows, getComparator(order, orderBy))
                //--------------------Filter by type------------------
                .filter((item) => {
                  if (filter !== "All") {
                    return item.type === filter
                  } else {
                    return item
                  }
                })
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row, index) => {
                  const isItemSelected = isSelected(row.id);
                  const labelId = `enhanced-table-checkbox-${index}`;

                  return (
                    <TableRow
                      hover
                      onClick={(event) => handleClick(event, row.id)}
                      role="checkbox"
                      aria-checked={isItemSelected}
                      tabIndex={-1}
                      key={row.id}
                      selected={isItemSelected}
                    >
                      <TableCell padding="checkbox">
                        <Checkbox
                          checked={isItemSelected}
                          inputProps={{ 'aria-labelledby': labelId }}
                        />
                      </TableCell>
                      <TableCell  component="th" id={labelId} scope="row" padding="none">
                        <Grid container justify="center">
                          <ListStatusItem maxWidth="130px" color={row.name === "Completed" ? "green" : row.name === "Ended" ? "red" : "yellow"}>
                            {row.name}
                          </ListStatusItem>
                        </Grid>
                      </TableCell>
                      <TableCell align="center">{row.calories}</TableCell>
                      <TableCell align="center">{row.type}</TableCell>
                      <TableCell align="center">
                        <div style={{ display: "flex", alignItems: "center",justifyContent:"center"}}>

                          <Avatar style={{marginRight:"20px"}} src={row.id % 2 ? "https://material-ui.com/static/images/avatar/3.jpg" : "https://material-ui.com/static/images/avatar/1.jpg"} />
                          {row.associated}
                        </div>

                      </TableCell>
                      <TableCell align="center">{row.date}</TableCell>
                    </TableRow>
                  );
                })}
              {emptyRows > 0 && (
                <TableRow style={{ height: (dense ? 33 : 53) * emptyRows }}>
                  <TableCell colSpan={6} />
                </TableRow>
              )}
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 25]}
          component="div"
          count={rows.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onChangePage={handleChangePage}
          onChangeRowsPerPage={handleChangeRowsPerPage}
        />
      </Paper>
      <FormControlLabel
        control={<Switch checked={dense} onChange={handleChangeDense} />}
        label="Dense padding"
      />
    </div>
  );
}



