const useStyles = makeStyles({
    root: {
        width: "100%",
    },
    container: {
        maxHeight: 440,
    },
});
function StickyHeadTable(props) {
    const classes = useStyles();
    const [page, setPage] = React.useState(0);
    const [rowsPerPage, setRowsPerPage] = React.useState(10);

    const handleChangePage = (event, newPage) => {
        setPage(newPage);
    };

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value);
        setPage(0);
    };

    return (
        <Paper className={classes.root}>
            <TableContainer className={classes.container}>
                <Table stickyHeader aria-label="sticky table">
                    <TableHead>
                        <TableRow>
                            {columns.map((column) => (
                                <TableCell
                                    key={column.id}
                                    align="center"
                                    style={{ minWidth: column.minWidth }}
                                >
                                    {column.label}
                                </TableCell>
                            ))}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows
                            .slice( page * rowsPerPage,page * rowsPerPage + rowsPerPage)
                            .map((row, index) => {
                                return (
                                    <TableRow
                                        hover
                                        role="checkbox"
                                        tabIndex={-1}
                                        key={row.cnp + index}
                                    >
                                        <TableCell align="center">
                                            <div
                                                onClick={() => {
                                                    props.history.push(
                                                        `${props.location.pathname}/${row.name}`
                                                    );
                                                }}
                                                style={{
                                                    display: "flex",
                                                    alignItems: "center",
                                                    justifyContent: "center",
                                                    cursor: "pointer",
                                                }}
                                            >
                                                <Avatar
                                                    style={{
                                                        marginRight: "20px",
                                                    }}
                                                    src={testAvatar}
                                                />
                                                <Link>
                                                    {row.name}
                                                </Link>
                                            </div>
                                        </TableCell>
                                        <TableCell align="center">
                                            {row.cnp}
                                        </TableCell>
                                        <TableCell align="center">
                                            {row.functie}
                                        </TableCell>
                                        <TableCell align="center">
                                            {row.organizatie}
                                        </TableCell>
                                        <TableCell align="center">
                                            {row.inactiv}
                                        </TableCell>
                                        <TableCell align="center">
                                            {row.centruDeProcesare}
                                        </TableCell>
                                        <TableCell align="center">
                                            {row.punctDeLucru}
                                        </TableCell>
                                        <TableCell align="center">
                                            {row.judet}
                                        </TableCell>
                                        <TableCell align="center">
                                            {row.nrLegitimatie}
                                        </TableCell>
                                    </TableRow>
                                );
                            })}
                    </TableBody>
                </Table>
            </TableContainer>
            <TablePagination
                rowsPerPageOptions={[10, 25, 100]}
                component="div"
                count={rows.length}
                rowsPerPage={rowsPerPage}
                page={page}
                onChangePage={handleChangePage}
                onChangeRowsPerPage={handleChangeRowsPerPage}
            />
        </Paper>
    );
}



export default withRouter(StickyHeadTable);