import React from 'react';


// components
import ComponentWrapper from "../../ComponentWrapper";
import RadioGroup from "./RadioGroup";
import StandAloneRadio from "./StandAloneRadio";
import ShowErrorRadio from "./ShowErrorRadio";

export default function Radio() {
    return (
        <div>
            <ComponentWrapper name="RadioGroup">
                <RadioGroup/>
            </ComponentWrapper>
            <ComponentWrapper name="Standalone radio buttons">
                <StandAloneRadio/>
            </ComponentWrapper>
            <ComponentWrapper name="Show error">
                <ShowErrorRadio/>
            </ComponentWrapper>
        </div>
    )
}
