import React from "react";

// components
import ComponentWrapper from "../../ComponentWrapper";
import BasicSwitch from "./BasicSwitch";
import SwitchLabel from "./SwitchLabel";
import SwitchFormGroup from "./SwitchFormGroup";
import SwitchSize from "./SwitchSize";

export default function Switch() {
    return (
        <div>
            <ComponentWrapper name="Basic switches">
                <BasicSwitch />
            </ComponentWrapper>
            <ComponentWrapper name="Switch Size">
                <SwitchSize />
            </ComponentWrapper>
            <ComponentWrapper name="Switch with FormControlLabel">
                <SwitchLabel />
            </ComponentWrapper>
            <ComponentWrapper name="Switch with FormGroup">
                <SwitchFormGroup />
            </ComponentWrapper>
        </div>
    );
}
