import React from 'react';

// components
import ComponentWrapper from "../../ComponentWrapper";
import BasicChip from "./BasicChip";
import OutlinedChip from "./OutlinedChip";
import ChipArray from "./ChipArray";
import SmallChip from "./SmallChip";
import OutlinedSmall from "./OutlinedSmallChip";

export default function Chip() {
    return (
        <div>
            <ComponentWrapper name="Chip">
                <BasicChip/>
            </ComponentWrapper>
            <ComponentWrapper name="Outlined Chip">
                <OutlinedChip/>
            </ComponentWrapper>
            <ComponentWrapper name="Chip Array">
                <ChipArray/>
            </ComponentWrapper>
            <ComponentWrapper name="Small Chip">
                <SmallChip/>
            </ComponentWrapper>
            <ComponentWrapper name="Outlined Small Chip">
                <OutlinedSmall/>
            </ComponentWrapper>
        </div>
    )
}
