import React from 'react';
import ComponentWrapper from "../../ComponentWrapper";

// components
import Linear from "./Linear";
import LinearAlternativeLabel from "./LinearAlternativeLabel";
import CustomizedStepper from "./CustomizedStepper";
import NonLinear from "./NonLinear";
import NonLinearAlternative from "./NonLinearAlternative";
import NonLinearError from "./NonLinearError";
import VerticalStepper from "./VerticalStepper";

export default function index() {
    return (
        <div>
            <ComponentWrapper name="Linear">
                <Linear />
            </ComponentWrapper>
            <ComponentWrapper name="Linear Alternative Label">
                <LinearAlternativeLabel />
            </ComponentWrapper>
            <ComponentWrapper name="Customized Stepper">
                <CustomizedStepper/>
            </ComponentWrapper>
            <ComponentWrapper name="Non Linear">
                <NonLinear/>
            </ComponentWrapper>
            <ComponentWrapper name="Non Linear Alternative">
                <NonLinearAlternative/>
            </ComponentWrapper>
            <ComponentWrapper name="Non Linear Error">
                <NonLinearError/>
            </ComponentWrapper>
            <ComponentWrapper name="Vertical Stepper">
                <VerticalStepper/>
            </ComponentWrapper>
        </div>

    )
}
