import React from "react";

// components
import ComponentWrapper from "../../ComponentWrapper";
import ContainedButtons from "./ContainedButtons";
import OutlinedButtons from "./OutlinedButtons";
import TextButtons from "./TextButtons";
import IconLabelButton from "./IconLabelButton";
import ButtonSizes from "./ButtonSizes";
import IconButtons from "./IconButtons";

export default function Buttons() {
    return (
        <div>
            <ComponentWrapper name="Contained buttons">
                <ContainedButtons />
            </ComponentWrapper>
            <ComponentWrapper name="OutlinedButtons">
                <OutlinedButtons />
            </ComponentWrapper>
            <ComponentWrapper name="Text buttons">
                <TextButtons />
            </ComponentWrapper>
            <ComponentWrapper name="Buttons with icons and label">
                <IconLabelButton />
            </ComponentWrapper>
            <ComponentWrapper name="Sizes">
                <ButtonSizes />
            </ComponentWrapper>
            <ComponentWrapper name="Icon Buttons">
                <IconButtons />
            </ComponentWrapper>
        </div>
    );
}
