import React from "react";

// component
import ComponentWrapper from "../../ComponentWrapper";
import FloatingButton from "./FloatingButton";
import FloatingActionButtonSize from "./FloatingActionButtonSize";
import FloatingActionButtonAnimation from "./FloatingActionButtonAnimation";

export default function index() {
    return (
        <div>
            <ComponentWrapper name="Floating Action Button">
                <FloatingButton />
            </ComponentWrapper>
            <ComponentWrapper name="Size">
                <FloatingActionButtonSize />
            </ComponentWrapper>
            <ComponentWrapper name="Animation">
                <FloatingActionButtonAnimation />
            </ComponentWrapper>
        </div>
    );
}
