import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { lighten, makeStyles } from '@material-ui/core/styles';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';
import DeleteIcon from '@material-ui/icons/Delete';
import { Button } from "@material-ui/core";
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';



const useToolbarStyles = makeStyles((theme) => ({
    root: {
      paddingLeft: theme.spacing(2),
      paddingRight: theme.spacing(1),
      justifyContent: "space-between"
    },
    highlight:
      theme.palette.type === 'light'
        ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85),
        }
        : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark,
        },
    title: {
      flex: '1 1 100%',
    },
    filterDiv: {
      display: "flex",
      minWidth: "200px"
    },
    formControl: {
      margin: theme.spacing(1),
      minWidth: 120,
    },
    selectEmpty: {
      marginTop: theme.spacing(2),
    },
  }));
  
  export default function EnhancedTableToolbar (props){
    const classes = useToolbarStyles();
    const { numSelected } = props;
    // State for filtering by date and type
    const [date, setDate] = React.useState('');
    const [type, setType] = React.useState("");
    const handleChange = (event, filterBy) => {
      if (filterBy === "type") {
        setType(event.target.value);
      } else if (filterBy === "date")
        setDate(event.target.value);
    };
  
    return (
      <Toolbar
        className={clsx(classes.root, {
          [classes.highlight]: numSelected > 0,
        })}
      >
        {numSelected > 0 ? (
          <Typography className={classes.title} color="inherit" variant="subtitle1" component="div">
            {numSelected} selected
          </Typography>
        ) : (
            // Filter by date and type----------------------------------
            <div className={classes.filterDiv} >
              {/* date */}
              <FormControl className={classes.formControl}>
                <InputLabel id="demo-simple-select-label">Date</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={date}
                  onChange={(e) => handleChange(e, "date")}
                >
                  <MenuItem value="today">Today</MenuItem>
                  {/* <MenuItem value={20}>Twenty</MenuItem>
                  <MenuItem value={30}>Thirty</MenuItem> */}
                </Select>
              </FormControl>
              {/* type */}
              <FormControl className={classes.formControl}>
                <InputLabel id="demo-simple-select-label">Type</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={type}
                  onChange={(e) => { props.handleFilterChange(e); handleChange(e, "type") }}
                >
                  <MenuItem value={"All"}>All</MenuItem>
                  <MenuItem value={"Reminder"}>Reminder</MenuItem>
                  <MenuItem value={"Call"}>Call</MenuItem>
                  <MenuItem value={"Event"}>Event</MenuItem>
                </Select>
              </FormControl>
            </div>
          )}
  
        {numSelected > 0 ? (
          <Tooltip title="Delete">
            <IconButton aria-label="delete">
              <DeleteIcon />
            </IconButton>
          </Tooltip>
        ) : (
            // Create task button
            <Button onClick={()=>props.handleCreateTask(true)} variant="contained" color="primary" size="large">
              Create Task
            </Button>
          )}
      </Toolbar>
    );
  };
  
  EnhancedTableToolbar.propTypes = {
    numSelected: PropTypes.number.isRequired,
  };
  
