import React from "react";

// components
import ComponentWrapper from "../../ComponentWrapper";
import SimpleSelect from "./SimpleSelect";
import SelectWithDialog from "./SelectWithDialog";
import GroupingSelect from "./GroupingSelect";

export default function Select() {
    return (
        <div>
            <ComponentWrapper name="Simple Select">
                <SimpleSelect />
            </ComponentWrapper>
            <ComponentWrapper name="Select With Dialog">
                <SelectWithDialog />
            </ComponentWrapper>
            <ComponentWrapper name="Grouping Select">
                <GroupingSelect />
            </ComponentWrapper>
        </div>
    );
}
