import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemText from "@material-ui/core/ListItemText";
import Collapse from "@material-ui/core/Collapse";
import ExpandLess from "@material-ui/icons/ExpandLess";
import ExpandMore from "@material-ui/icons/ExpandMore";
import { withRouter } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        maxWidth: 360,
        backgroundColor: theme.palette.background.paper,
    },
    nested: {
        paddingLeft: theme.spacing(4),
    },
}));

function NestedList(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    const handleClick = () => {
        setOpen(!open);
    };
    return (
        <List
            component="nav"
            aria-labelledby="nested-list-subheader"
            className={classes.root}
        >
            <ListItem button onClick={handleClick}>
                <ListItemText primary={props.listItemText} />
                {open ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={open} timeout="auto" unmountOnExit>
                {props.listItems.map((item) => {
                    return (
                        <List
                            onClick={() =>
                                props.history.push(`/${item.toLowerCase()}`)
                            }
                            key={item}
                            component="div"
                            disablePadding
                        >
                            <ListItem selected={props.location.pathname.includes(item.toLowerCase())} button className={classes.nested}>
                                <ListItemText  primary={item} />
                            </ListItem>
                        </List>
                    );
                })}
            </Collapse>
        </List>
    );
}


export default withRouter(NestedList);