import React from "react";
import Panel from "./Panel";
import testAvatar from "../../../assets/avatar.jpg";
import { withRouter } from "react-router-dom";
import PanelContent from "./PanelContent";
import { IconButton } from "@material-ui/core";
import ChevronLeftIcon from "@material-ui/icons/ChevronLeft";
import PanelActions from "./PanelActions";
import { TextField } from "@material-ui/core";
import { Grid } from "@material-ui/core";
import Avatar from "@material-ui/core/Avatar";
import { Checkbox } from "@material-ui/core";
import { FormControlLabel } from "@material-ui/core";
import { Button } from "@material-ui/core";

import CustomStepper from "../CustomStepper/CustomStepper";

function PanelOpen(props) {
    return (
        <Panel>
            <PanelActions>
                <IconButton
                    onClick={() => {
                        props.history.goBack();
                    }}
                >
                    <ChevronLeftIcon fontSize="small" />
                </IconButton>
                {props.match.params.id}
            </PanelActions>
            <PanelContent>
                <Grid container style={{ padding: "30px" }}>
                    <Grid container justify="center">
                        <Grid item container style={{maxWidth:"1200px"}}>
                            <CustomStepper />
                        </Grid>
                    </Grid>
                    <Grid item container direction="column" alignItems="center" xs={12} md={5}>
                        <Avatar
                            style={{
                                marginTop: "100px",
                                height: "200px",
                                width: "200px",
                                marginBottom: "30px"
                            }}
                            src={testAvatar}
                        />
                        <Button color="primary" variant="contained">
                            INCARCARE
                            </Button>
                    </Grid>
                    <Grid container direction="column" item xs={12} md={5}>
                        <TextField
                            style={{ marginBottom: "20px" }}
                            label="Nume"
                        />
                        <TextField
                            style={{ marginBottom: "20px" }}
                            label="Prenume"
                        />
                        <TextField
                            style={{ marginBottom: "20px" }}
                            label="CNP"
                        />
                        <TextField
                            style={{ marginBottom: "20px" }}
                            label="Functie"
                        />
                        <TextField
                            style={{ marginBottom: "20px" }}
                            label="Organizatie"
                        />
                        <FormControlLabel
                            style={{ marginBottom: "20px" }}
                            control={<Checkbox color="primary" />}
                            label="Inactiv"
                        />
                        <TextField
                            style={{ marginBottom: "20px" }}
                            label="Centru de procesare"
                        />
                        <TextField
                            style={{ marginBottom: "20px" }}
                            label="Punct de lucru"
                        />
                        <TextField style={{ marginBottom: "20px" }} label="Judet" />
                        <Grid container justify="space-between">
                            <Button onClick={() => {
                                props.history.goBack();
                            }} variant="contained">Renunta</Button>
                            <Button variant="contained" color="primary">Salveaza</Button>
                        </Grid>
                    </Grid>
                </Grid>
            </PanelContent>
        </Panel>
    );
}

export default withRouter(PanelOpen);
