import React from "react";

// components
import ComponentWrapper from "../../ComponentWrapper";
import SimpleCard from "./SimpleCard";
import OutlinedCard from "./OutlinedCard";
import ImgCard from "./ImgCard";

export default function Card() {
    return (
        <div>
            <ComponentWrapper name="Simple Card">
                <SimpleCard />
            </ComponentWrapper>
            <ComponentWrapper name="Outlined Card">
                <OutlinedCard />
            </ComponentWrapper>
            <ComponentWrapper name="ImgCard">
                <ImgCard />
            </ComponentWrapper>
        </div>
    );
}
