import React from 'react';

// components
import ComponentWrapper from "../../ComponentWrapper";
import Collapse from "./Collapse";
import Fade from "./Fade";
import Grow from "./Grow";
import Slide from "./Slide";
import Zoom from "./Zoom";


export default function index() {
    return (
        <div>
            <ComponentWrapper name="Collapse">
                <Collapse/>
            </ComponentWrapper>
            <ComponentWrapper name="Fade">
                <Fade/>
            </ComponentWrapper>
            <ComponentWrapper name="Grow">
                <Grow/>
            </ComponentWrapper>
            <ComponentWrapper name="Slide">
                <Slide/>
            </ComponentWrapper>
            <ComponentWrapper name="Zoom">
                <Zoom/>
            </ComponentWrapper>
        </div>
    )
}
