import React from "react";

import ComponentWrapper from "../../ComponentWrapper";
import SimpleAppBar from "./SimpleAppBar";
import AppBarPrimarySearch from "./AppBarPrimarySearch";
import AppBarMenu from "./AppBarMenu";
import AppBarSearch from "./AppBarSearch";


export default function AppBar() {
    return (
        <div>
            <ComponentWrapper name="Simple App Bar">
                <SimpleAppBar />
            </ComponentWrapper>
            <ComponentWrapper name="App Bar Menu">
                <AppBarMenu />
            </ComponentWrapper>
            <ComponentWrapper name="App Bar with a primary search field">
                <AppBarPrimarySearch />
            </ComponentWrapper>
            <ComponentWrapper name="App Bar with search field">
                <AppBarSearch />
            </ComponentWrapper>
        </div>
    );
}
